import React from 'react'

const Meta = () => {
  return (
    <div 
    className='metaRow'
    id='meta'>
      <div className="col-12 socio p-2 fb ">
      <i class="bi bi-facebook"><span>facebook</span></i>
      </div>
      <div className="col-12 socio p-2 insta">
      <i class="bi bi-instagram"><span>Instagram</span></i>
      </div>
      <div className="col-12 socio p-2 whatsapp">
      <i class="bi bi-whatsapp"><span>Whatsapp</span></i>
      </div>
      <div className="col-12 socio p-2 git">
      <i class="bi bi-github"><span>GitHub</span></i>
      </div>
      <div className="col-12 p-2 socio linkedin">
      <i class="bi bi-linkedin"><span>Linkedin</span></i>
      </div>
      <div className="col-12 p-2 socio snap">
      <i class="bi bi-snapchat"><span>Snapchat</span></i>
      </div>

      
    </div>
  )
}
export default Meta